import {
  NgModule,
  ModuleWithProviders,
  SkipSelf,
  Optional,
} from '@angular/core';
import { CommonModule } from '@angular/common';

import { Toast } from './toast-component';
import { TOAST_CONFIG } from './toast-token';
import { ToastrService } from './toastr-service';
import { GlobalConfig } from './toastr-config';
import { OverlayContainer } from '../overlay/overlay-container';
import { Overlay } from '../overlay/overlay';



export class ToastrModule {
  constructor(  parentModule: ToastrModule) {
    if (parentModule) {
      throw new Error('ToastrModule is already loaded. It should only be imported in your application\'s main module.');
    }
  }
  static forRoot(config?: GlobalConfig): ModuleWithProviders {
    return {
      ngModule: ToastrModule,
      providers: [
        { provide: TOAST_CONFIG, useValue: config },
        OverlayContainer,
        Overlay,
        ToastrService,
      ]
    };
  }
static decorators: DecoratorInvocation[] = [
{ type: NgModule, args: [{
  imports: [CommonModule],
  exports: [Toast],
  declarations: [Toast],
  entryComponents: [Toast],
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: ToastrModule, decorators: [{ type: Optional }, { type: SkipSelf }, ]},
];
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
